.company-page {
    &__top {
        display: flex;
        margin-top: 8px;
        @include sm {
            flex-flow: column; } }
    &__logo {
        flex: 0 0 120px;
        @include sm; }

    &__location {
        margin-bottom: 18px;
        .icon {
            margin-right: 3px;
            font-size: 12px; } }
    &__descr {
        padding-left: 40px;
        @include sm {
            padding-left: 0; } }
    &__title {
        font-size: 0;
        margin-bottom: 2px;
        span:first-child {
            display: inline-block;
            vertical-align: middle;
            font-size: 25px;
            font-weight: 700; } }
    &__cite-link {
        display: block;
        margin-top: 4px;
        margin-bottom: 8px; }
    &__buttons {
        margin-bottom: 26px;
        font-size: 0;
        & > * {
            margin-right: 9px; } }
    &__row {
        display: flex;
        justify-content: center;
        margin: 0 -15px;
        justify-content: flex-start;
        @include md {
            padding-left: 43px; }
        @include sm {
            flex-flow: column;
            align-items: center; } }
    &__col {
        flex: 0 0 300px;
        width: 300px;
        margin: 0 15px;
        .card,
        .collection {
            margin-bottom: 40px; } }

    &__private-collection {
        margin-bottom: 100px;
        margin-top: 33px;
        h3 {
            margin-bottom: 34px;
            font-size: 20px;
            font-weight: 700;
            line-height: 1.25;
            @include md {
                padding-left: 43px; }
            @include sm {
                text-align: center; } } } }







.follow-mark {
    display: inline-block;
    vertical-align: middle;
    font-size: 0;
    @include circle(16);
    margin-left: 10px;
    background-color: $blue;
    color: $white;
    position: relative;
    .icon {
        font-size: 6px;
        @include vcenter; } }

.instagram-info {
    color: $grey;
    &__nickname,
    &__followers {
        position: relative;
        margin-right: 17px;
        &:after {
            content: '.';
            position: absolute;
            bottom: 1px;
            right: -10px; } } }

